export default {
  props: {
    accountId: String,
    assetChain: String,
    receiveFeeProblem: Boolean,
    toAssetChain: String
  },
  computed: {
    accountUrl() {
      return `/accounts/${this.accountId}/${this.assetChain}/receive`;
    },
    toAccountUrl() {
      return `/accounts/${this.accountId}/${this.toAssetChain}/receive`;
    },
    isSameChain() {
      return this.assetChain === this.toAssetChain;
    }
  }
};