import { mapActions } from 'vuex';
export default {
  props: ['network', 'walletId', 'asset', 'updateQuotes'],
  methods: {
    ...mapActions(['enableAssets']),
    toggleAsset() {
      const params = {
        network: this.network,
        walletId: this.walletId,
        assets: [this.asset]
      };
      this.enableAssets(params);
      this.updateQuotes();
    }
  }
};