import { mapState } from 'vuex';
import Modal from '@/components/Modal';
import SwapProviderLabel from '@/components/SwapProviderLabel.vue';
import { getSwapProviderConfig, getSwapProviderInfo } from '@liquality/wallet-core/dist/src/swaps/utils';
import { getSwapProviderIcon } from '@/utils/swaps';
import { buildConfig } from '@liquality/wallet-core';
export default {
  components: {
    Modal,
    SwapProviderLabel
  },
  computed: {
    ...mapState(['activeNetwork']),
    unsupportedProviders() {
      // TODO:: We will need to remove this computed prop after these providers are removed from wallet-core
      return new Array('liquality', 'liqualityBoostNativeToERC20', 'liqualityBoostERC20toNative');
    },
    providers() {
      return Object.entries(buildConfig.swapProviders[this.activeNetwork]).map(([provider, providerConfig]) => {
        return {
          id: provider,
          name: providerConfig.name,
          ...getSwapProviderInfo(this.activeNetwork, provider)
        };
      }).filter(provider => !this.unsupportedProviders.includes(provider.id));
    },
    uniqueProvides() {
      return [...new Map(this.providers.map(item => [item['name'], item])).values()];
    }
  },
  methods: {
    getProviderName(provider) {
      const config = getSwapProviderConfig(this.activeNetwork, provider);
      return config.name;
    },
    getProviderIcon(provider) {
      return getSwapProviderIcon(this.activeNetwork, provider);
    },
    getProviderInfo(provider) {
      return getSwapProviderInfo(this.activeNetwork, provider);
    }
  }
};