import "core-js/modules/es.string.replace-all.js";
import { getAssetColorStyle } from '@liquality/wallet-core/dist/src/utils/asset';
import { getAssetIcon } from '@/utils/asset';
import { dpUI, formatFiatUI } from '@liquality/wallet-core/dist/src/utils/coinFormatter';
import ChevronRightIcon from '@/assets/icons/chevron_right_gray.svg';
import AccountTooltip from '@/components/AccountTooltip';
export default {
  components: {
    ChevronRightIcon,
    AccountTooltip
  },
  data() {
    return {
      showAmountsInFiat: false
    };
  },
  props: ['account', 'asset', 'available', 'sendAmount', 'sendAmountFiat', 'min', 'max', 'minFiat', 'maxFiat', 'showErrors', 'amountError', 'disabled', 'amountOption'],
  methods: {
    dpUI,
    formatFiatUI,
    getAssetColorStyle,
    getAssetIcon,
    toggleShowAmountsFiat() {
      this.showAmountsInFiat = !this.showAmountsInFiat;
    },
    setSendAmount(amount) {
      this.$emit('send-amount-change', amount);
    },
    assetIconClick() {
      this.$emit('from-asset-click');
    },
    sendAmountFiatInputFormat() {
      // in case of undefined/NaN value for amount in fiat, present the placeholder value
      return !this.sendAmountFiat ? '' : this.sendAmountFiat.replaceAll(',', '');
    }
  }
};